import React from "react";
import { CheckStatus, Logout, Login } from "Auth";
import Signin from "./Signin";
import Signout from "./Signout";
import { useDispatch } from "react-redux";
import { startLoading, stopLoading } from "Redux/Actions/loading";

const Auth = () => {
  const dispatch = useDispatch();
  const handleLogin = async (e) => {
    e.preventDefault();
    dispatch(startLoading());
    await Login(e.target.email.value, e.target.password.value);
    dispatch(stopLoading());
  };

  return (
    <div style={{ borderBottom: "3px solid black", paddingBottom: "5px" }}>
      {CheckStatus() ? (
        <Signout handleSubmit={Logout} />
      ) : (
        <Signin handleSubmit={handleLogin} />
      )}
    </div>
  );
};

export default Auth;
