import { collection, addDoc}  from "firebase/firestore";
import { db } from "Firebase/Config";
import { toast } from "react-toastify";

const Create = async (nameCollection, data, onStart, onSuccess, isFailed,isToast) => {
  onStart?.();

  try {
    await addDoc(collection(db, nameCollection), await data);
    onSuccess?.();
    isToast&&toast("Success", { type: "success" });
  } catch (err) {
    isFailed?.();
    isToast&&toast(err, { type: "error" });
  }
};
export default Create;
