/* eslint-disable import/no-anonymous-default-export */
import { LOADING_START, LOADING_FINISHED } from "Redux/Types/loadingTypes";

const initialStateLoading = {
  loading: false,
};

export default (state = initialStateLoading, action) => {
  switch (action.type) {
    case LOADING_START:
      return { loading: true };
    case LOADING_FINISHED:
      return { loading: false };
    default:
      return state;
  }
};
