import { ForgotPassword } from "Auth";
import React from "react";
import { Link } from "react-router-dom";
import { useHistory } from 'react-router';

const ForgetPassword = () =>{
const {push}= useHistory()  
  const handleSubmit=async(e)=>{
    e.preventDefault();
    await ForgotPassword(e.target.email.value);
    push('/dashboard')
  }

      return <form onSubmit={handleSubmit}  className="d-flex flex-wrap align-items-center justify-content-end">
        <input
          type="email"
          className="rounded mb-2"
          placeholder="Email"
          name="email"
          required
        />
         <Link to="/dashboard" >Je me souviens</Link>
        <button
          type="submit"
          className="btn  rounded px-4 btn-sm btn-primary"
        >
          Envoyé
        </button>
      </form>}

export default ForgetPassword;