import React, { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { startLoading, stopLoading } from "Redux/Actions/loading";
import "./BackgroundVideo.css";

const BackgroundVideo = ({
  width = "100%",
  height = "100%",
  objectFit = "cover",
  top = "-0.5rem",
  video,
  position = "fixed",
}) => {
  const dispatch = useDispatch();
  const [loaded, setLoaded] = useState("");
  useEffect(() => {
    loaded!=='canplaythrough' ? dispatch(startLoading()): dispatch(stopLoading()) ;
  },[loaded,dispatch]);
  return (
    <div
      className="main col-12 col-md-10 d-flex justify-content-center "
      style={{ position, zIndex: "-1" }}
    >
      <video
        autoPlay
        loop
        muted
        style={{ width, height, objectFit, top }}
        onCanPlayThrough={(e) => setLoaded(e.type)}
      >
        <source src={video} type="video/mp4" />
      </video>
    </div>
  );
};

export default BackgroundVideo;
