import React, { useState } from "react";
import "./Form.css";
import { IoSend } from "react-icons/io5";
import User from "Models/user";
import Loader from "Components/LoaderContainer/Loader";
import { useHistory } from "react-router";
import Create from "CRUD/Create";
import { ListOptionForm } from "Constants/ListOptionForm";

const Form = () => {
  const [load, setLoad] = useState(false);
  const { push } = useHistory();
  const isToast = true;
  const handleSubmit = async (e) => {
    e.preventDefault();

    await Create(
      "users",
      User(e),
      () => {
        setLoad(true);
      },
      () => {
        setLoad(false);
        push("/");
      },
      () => {
        setLoad(false);
      },
      isToast
    );
  };

  return (
    <>
      {load ? (
        <Loader />
      ) : (
        <form onSubmit={handleSubmit}>
          <select
            className="form-select mb-1"
            style={{ maxWidth: "100%", overflow: "hidden" }}
            size={1}
            name="gender"
          >
            <option selected disabled hidden>
              Genre ?
            </option>
            <option value="Mme">Mme</option>
            <option value="M">M</option>
          </select>
          <input
            type="text"
            className="form-control mb-1"
            placeholder="Prénon"
            name="firstName"
          />
          <input
            type="text"
            className="form-control mb-1"
            placeholder="Nom*"
            name="lastName"
          />
          <input
            type="phone"
            className="form-control mb-1"
            placeholder="Téléphone*"
            name="phone"
          />
          <input
            type="email"
            className="form-control mb-1"
            id="exampleFormControlInput1"
            placeholder="Votre email@example.com*"
            name="email"
          />

          <select
            className="form-select  mb-1"
            style={{ maxWidth: "100%", overflow: "hidden" }}
            size={1}
            name="chose"
          >
            <option selected disabled hidden>
              votre demande ?
            </option>

            {ListOptionForm.map(({ value }, i) => (
              <option key={i} value={value}>
                {value}
              </option>
            ))}
          </select>

          <textarea
            className="form-control mb-1"
            placeholder="Dites-nous votre besoin..."
            rows="3"
            name="note"
          ></textarea>
          <span className="text-white h6 row text-truncate g-0">
            (*) champs obligatoire
          </span>
          <div className="d-flex justify-content-end">
            <button
              type="submit"
              className="btn rounded px-4"
              style={{ background: "rgba(255, 255, 255, 0.7)" }}
            >
              <IoSend />
            </button>
          </div>
        </form>
      )}
    </>
  );
};

export default Form;
