import React from "react";
import { BiUserCircle } from "react-icons/bi";
import { Link } from "react-router-dom";

const Signin = ({handleSubmit}) => 
      <form onSubmit={handleSubmit}  className="d-flex flex-wrap align-content-center justify-content-around justify-content-lg-end">
        <input
          type="email"
          className="rounded mb-2"
          placeholder="Email"
          name="email"
          required
        />
        <input
          type="password"
          className="rounded mb-2"
          placeholder="Password"
          name="password"
          required
        />
        <Link to="/dashboard/forgetPassword" >Mots de passe oublier</Link>
        <BiUserCircle className="text-red-color fs-1 my-auto mx-2 " />
        <button
          type="submit"
          className="btn  rounded px-4 btn-sm btn-primary"
        >
          Se connecté
        </button>
      </form>

 

export default Signin;
